
body {
    background: #30cd9c;
    background-image: linear-gradient(90deg, #30cd9c, #30cd9c);
}
.box {
    padding: 2%;
    width: 80%;
    background: #FFF;
    margin: 40px auto;
}

/*==================================================
 * Effect 1
 * ===============================================*/
.effect1 {
    -webkit-box-shadow: 0 10px 6px -6px #777;
    -moz-box-shadow: 0 10px 6px -6px #777;
    box-shadow: 0 10px 6px -6px #777;
}

footer{
    bottom: 0;
    margin-bottom: 2%;
    width: 100%;
    height: 2.5rem;            /* Footer height */
}

.stockCard{
    -webkit-box-shadow: 0 10px 6px -6px #777;
    -moz-box-shadow: 0 10px 6px -6px #777;
    box-shadow: 0 10px 6px -6px #777;
    border: #e8e8e8;
    border-style: solid;
    border-width: 1px;
}


.tradingview-widget-container,
.tradingview-widget-container__widget,
.tradingview-widget-container__widget * {
    background-color: black ;
    color: white;
}
